import React from 'react'
import Header from '../Header'
import Sidebar from '../Sidebar'

const Withdraw = () => {
    return (
        <div className='container'>

        <Header />
        <div className='container row' style={{ paddingLeft: "0px", paddingRight: "0px", marginLeft: "0px" }}>
          <div className="col-lg-2 col-md-3" style={{ paddingLeft: "0px" }}>
            <Sidebar />
          </div>
  
          <div className="col-lg-10 col-md-9" style={{ paddingLeft: "0px", marginTop: "60px" }}>
          <div className="mt-3">
          <div className="mb-3">
              <h2 className="h1 mb-0 text-capitalize d-flex align-items-center gap-2">
                  <img width={20} src="https://6valley.6amtech.com/public/assets/back-end/img/withdraw-icon.png" alt />
                  Withdraw
              </h2>
          </div>
          <div className="row">
              <div className="col-md-12">
                  <div className="card">
                      <div className="card-header flex-wrap gap-2">
                          <h5 className="mb-0 text-capitalize">Withdraw Request Table
                              <span className="badge badge-soft-dark radius-50 fz-12 ml-1">3</span>
                          </h5>
                          <select name="withdraw_status_filter" onchange="status_filter(this.value)" className="custom-select max-w-200">
                              <option value="all">All</option>
                              <option value="approved">Approved</option>
                              <option value="denied">Denied</option>
                              <option value="pending">Pending</option>
                          </select>
                      </div>
                      <table id="datatable" style={{ textAlign: 'left' }} className="table table-hover table-borderless table-thead-bordered table-nowrap table-align-middle card-table">
                          <thead className="thead-light thead-50 text-capitalize">
                              <tr>
                                  <th>SL</th>
                                  <th>Amount</th>
                                  <th>Request time</th>
                                  <th>Status</th>
                                  <th className="text-center">Action</th>
                              </tr>
                          </thead>
                          <tbody>
                              <tr>
                                  <td>1</td>
                                  <td>$500.00</td>
                                  <td>October 12th, 2022</td>
                                  <td>
                                      <label className="badge badge-soft-danger">Denied</label>
                                  </td>
                                  <td className="text-center">
                                      <span className="btn btn--primary btn-sm disabled">
                                          Close
                                      </span>
                                  </td>
                              </tr>
                              <tr>
                                  <td>2</td>
                                  <td>$600.00</td>
                                  <td>October 12th, 2022</td>
                                  <td>
                                      <label className="badge badge-soft-success">Approved</label>
                                  </td>
                                  <td className="text-center">
                                      <span className="btn btn--primary btn-sm disabled">
                                          Close
                                      </span>
                                  </td>
                              </tr>
                              <tr>
                                  <td>3</td>
                                  <td>$500.00</td>
                                  <td>October 12th, 2022</td>
                                  <td>
                                      <label className="badge badge-soft--primary">Pending</label>
                                  </td>
                                  <td className="text-center">
                                      <button id="https://6valley.6amtech.com/seller/business-settings/withdraw/cancel/9" onclick="close_request('https://6valley.6amtech.com/seller/business-settings/withdraw/cancel/9')" className="btn btn--primary btn-sm">
                                          Close
                                      </button>
                                  </td>
                              </tr>
                          </tbody>
                      </table>
                  </div>
                  <div className="table-responsive mt-4">
                      <div className="px-4 d-flex justify-content-lg-end">
                      </div>
                  </div>
              </div>
          </div>
      </div>
          </div>
  
        </div>
           

        </div>
    )
}

export default Withdraw
