import React from 'react'
import Header from '../Header'
import Sidebar from '../Sidebar'
import { Link } from 'react-router-dom'

const Editbanner = () => {
  
  return (
    <div className='container'>       

    <Header />
    <div className='container row' style={{ paddingLeft: "0px", paddingRight: "0px", marginLeft: "0px" }}>

        <div className="col-lg-2 col-md-3" style={{ paddingLeft: "0px" }}>
            <Sidebar />
        </div>

        <div className="col-lg-10 col-md-9" style={{ paddingLeft: "0px", marginTop: "60px" }}>
            <div className=" mt-3">
            <div className="d-flex justify-content-between mb-3">
            <div>
              <h2 className="h1 mb-1 text-capitalize d-flex align-items-center gap-2">
                <img width={20} src="https://6valley.6amtech.com/public/assets/back-end/img/banner.png" alt />
                Banner update form
              </h2>
            </div>
            <div>
              <Link className="btn btn--primary text-white" to="/bannerlist">
                 Back</Link>
            </div>
          </div>
          <div className="row" style={{textAlign: 'left'}}>
            <div className="col-md-12">
              <div className="card">
                <div className="card-body">
                  <form  className="banner_form">
                    <input type="hidden" name="_token" defaultValue="M5Ms1Z4GEx6hqRPEuULXw4EVAXN9PGreUwLBXESa" /> <input type="hidden" name="_method" defaultValue="put" /> <div className="row g-3">
                      <div className="col-md-6">
                        <div className="form-group">
                          <input type="hidden" id="id" name="id" />
                        </div>
                        <div className="form-group">
                          <label htmlFor="name" className="title-color text-capitalize">Banner type</label>
                          <select className="js-example-responsive form-control w-100" name="banner_type" required id="banner_type_select">
                            <option value="Main Banner">Main Banner</option>
                            <option value="Popup Banner">Popup Banner</option>
                            <option value="Footer Banner">Footer Banner</option>
                            <option value="Main Section Banner" selected>Main Section Banner</option>
                          </select>
                        </div>
                        <div className="form-group mb-3">
                          <label htmlFor="name" className="title-color text-capitalize">Banner URL</label>
                          <input type="url" name="url" className="form-control" id="url" required placeholder="Enter url" defaultValue="https://codecanyon.net/item/6valley-multivendor-ecommerce-complete-ecommerce-mobile-app-web-and-admin-panel/31448597?s_rank=1" />
                        </div>
                        <div className="form-group">
                          <label htmlFor="resource_id" className="title-color text-capitalize">Resource type</label>
                          <select onchange="display_data(this.value)" className="js-example-responsive form-control w-100" name="resource_type" required>
                            <option value="product" selected>Product</option>
                            <option value="category">Category</option>
                            <option value="shop">Shop</option>
                            <option value="brand">Brand</option>
                          </select>
                        </div>
                  
                        <div className="form-group mb-0" id="resource-category" style={{display: 'none'}}>
                          <label htmlFor="name" className="title-color text-capitalize">Category</label>
                          <select className="js-example-responsive form-control w-100" name="category_id">
                            <option value={39}>Bags &amp; Shoes</option>
                            <option value={34}>Home, Pet &amp; Appliances</option>
                            <option value={28}>Phones &amp; Telecom</option>
                            <option value={27}>Computer, Office &amp; Security</option>
                            <option value={26}>Mobile Accessories</option>
                            <option value={12}>Toys , Kids &amp; Babies</option>
                            <option value={25}>Beauty, Health &amp; Hair</option>
                            <option value={24}>Jewelry &amp; Watches</option>
                            <option value={16}>ebook</option>
                            <option value={15}>Women's fashion</option>
                            <option value={11}>Home Improvement &amp; Tools</option>
                            <option value={14}>Outdoor Fun &amp; Sports</option>
                            <option value={13}>Men's fashion</option>
                          </select>
                        </div>
                        <div className="form-group mb-0" id="resource-shop" style={{display: 'none'}}>
                          <label htmlFor="shop_id" className="title-color text-capitalize">Shop</label>
                          <select className="js-example-responsive form-control w-100" name="shop_id">
                            <option value={1} selected>Deluxe Online</option>
                            <option value={2}>Mart Morning</option>
                            <option value={3}>Wellness Fair</option>
                            <option value={4}>Bicycle Shop</option>
                            <option value={5}>KR Fashion</option>
                            <option value={6}>Country Fair</option>
                            <option value={7}>Tech Shop</option>
                            <option value={8}>Royal Crown</option>
                            <option value={9}>Super Store Online</option>
                            <option value={10}>Digital House</option>
                          </select>
                        </div>
                        <div className="form-group mb-0" id="resource-brand" style={{display: 'none'}}>
                          <label htmlFor="brand_id" className="title-color text-capitalize">Brand</label>
                          <select className="js-example-responsive form-control w-100" name="brand_id">
                            <option value={1} selected>i Bird</option>
                            <option value={4}>Agron</option>
                            <option value={5}>Triangle</option>
                            <option value={6}>Estro</option>
                            <option value={7}>Ohoenix</option>
                            <option value={8}>Waltro</option>
                            <option value={9}>JK</option>
                            <option value={10}>Crave</option>
                            <option value={11}>Framerce</option>
                            <option value={12}>Fashion</option>
                            <option value={13}>S.Cube</option>
                            <option value={14}>Estha dot</option>
                            <option value={17}>Digital Product</option>
                          </select>
                        </div>
                      </div>
                      <div className="col-md-6 d-flex flex-column justify-content-center">
                        <div>
                          <center className="mx-auto">
                            <div className="uploadDnD">
                              <div className="form-group inputDnD input_image input_image_edit" style={{backgroundImage: 'url("https://6valley.6amtech.com/storage/app/public/banner/2023-06-13-64885dd743c93.png")'}} data-title>
                                <input type="file" name="image" className="form-control-file text--primary font-weight-bold" onchange="readUrl(this)" accept=".jpg, .png, .jpeg, .gif, .bmp, .webp |image/*" />
                              </div>
                            </div>
                          </center>
                          <label htmlFor="name" className="title-color text-capitalize">
                            <span className="input-label-secondary cursor-pointer" data-toggle="tooltip" data-placement="right" title data-original-title="Banner image ratio is not same for all sections in website Please review the ratio before upload">
                              <img width={16} src="https://6valley.6amtech.com/public/assets/back-end/img/info-circle.svg" alt className="m-1" />
                            </span>
                            Banner image
                          </label>
                          <span className="text-info" id="theme_ratio">( Ratio 4:1 )</span>
                          <p>Banner Image ratio is not same for all sections in website. Please review the ratio before upload</p>
                        </div>
                      </div>
                      <div className="col-md-12 d-flex justify-content-end gap-3">
                        <button type="reset" className="btn btn-secondary px-4">Reset</button>
                        <button type="submit" className="btn btn--primary px-4">Update</button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
            </div>
        </div>



    </div>





    </div>
  )
}

export default Editbanner
