import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import './manubar.css'


const Login = () => {
    let navigate=useNavigate()
    let login=()=>{
        navigate(`/home`)
    }

    let [passwordtype, setpasswordtype] = useState("password")
    let [iconstatus,seticonstatus]=useState(false)



    let passwordicon = () => {
        console.log("hello")
        setpasswordtype("text")
        seticonstatus(true)

    }
    let passwordicon1 = () => {
        seticonstatus(false)
        setpasswordtype("password")
    }
    return (
        <div>
            <div className="position-fixed top-0 right-0 left-0 bg-img-hero __inline-1" style={{ backgroundImage: 'url(https://6valley.6amtech.com/public/assets/admin/svg/components/abstract-bg-4.svg)' }}>
                <figure className="position-absolute right-0 bottom-0 left-0">
                    <svg preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 1921 273">
                        <polygon fill="#fff" points="0,273 1921,273 1921,0 " />
                    </svg>
                </figure>
            </div>
            <div className="container py-5 py-sm-7">
                <a className="d-flex justify-content-center mb-5" href="javascript:">
                    <img className="z-index-2" height={40} src="./logonew.png" alt="Logo" onerror="this.src='https://6valley.6amtech.com/public/assets/back-end/img/400x400/img2.jpg'" />
                </a>
                <div className="row justify-content-center">
                    <div className="col-md-7 col-lg-5">
                        <div className="card card-lg mb-5">
                            <div className="card-body">
                                <form id="form-id" action="" method="post">
                                    <input type="hidden" name="_token" defaultValue="S8nZf6sgc19bujWGQYaC5CglQduIwcKSiTkULZ1K" />
                                    <div className="text-center">
                                        <div className="mb-5">
                                            <span>( Admin Login)</span>
                                        </div>
                                    </div>
                                    <input type="hidden" className="form-control mb-3" name="role" defaultValue="admin" />
                                    <div className="js-form-message form-group">
                                        <label className="input-label" htmlFor="signinSrEmail">email</label>
                                        <input type="email" className="form-control form-control-lg" name="email" id="signinSrEmail" tabIndex={1} placeholder="email@address.com" aria-label="email@address.com" required data-msg="Please enter a valid email address." />
                                    </div>
                                    <div className="js-form-message form-group">
                                        <label className="input-label" htmlFor="signupSrPassword" tabIndex={0}>
                                            <span className="d-flex justify-content-between align-items-center">
                                                Password
                                            </span>
                                        </label>
                                        <div className="input-group input-group-merge iconinput">
                                            <input type={passwordtype == "password" ? ("password") : ("text")} className="js-toggle-password form-control form-control-lg" name="password" />
                                           
                                           {iconstatus==false?(<i onClick={passwordicon} class="fa fa-eye eyeicon" aria-hidden="true"></i>):( <i onClick={passwordicon1} class="fa fa-eye-slash eyeicon" aria-hidden="true"></i>)} 
                                          
                                           
                                        </div>
                                    </div>

                                    <div id="recaptcha_element" className="w-100;" data-type="image" />
                                    <br />
                                </form>
                                <button type="submit" className="btn btn-lg btn-block btn--primary" onClick={login}>Sign in</button>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default Login
