import React from 'react'
import Sidebar from '../Sidebar'
import Header from '../Header'
import { Link } from 'react-router-dom'

const Mybankinfo = () => {
    return (
        <div className='container'>
        <Header />
        <div className='container row' style={{ paddingLeft: "0px", paddingRight: "0px", marginLeft: "0px" }}>
          <div className="col-lg-2 col-md-3" style={{ paddingLeft: "0px" }}>
            <Sidebar />
          </div>
  
          <div className="col-lg-10 col-md-9" style={{ paddingLeft: "0px", marginTop: "60px" }}>
          <div className="mt-3">
          <div className="mb-3">
              <h2 className="h1 mb-0 text-capitalize d-flex align-items-center gap-2">
                  <img width={20} src="https://6valley.6amtech.com/public/assets/back-end/img/my-bank-info.png" alt />
                  My bank info
              </h2>
          </div>
          <div className="row mt-3">
              <div className="col-md-12">
                  <div className="card" style={{ textAlign: 'left' }}>
                      <div className="border-bottom d-flex gap-3 flex-wrap justify-content-between align-items-center px-4 py-3">
                          <div className="d-flex gap-2 align-items-center">
                              <img width={20} src="https://6valley.6amtech.com/public/assets/back-end/img/bank.png" alt />
                              <h3 className="mb-0">Bank Information</h3>
                          </div>
                          <div className="d-flex gap-2 align-items-center">
                              <Link to="/editmybankinfo" className="btn btn--primary">
                                  Edit
                              </Link>
                          </div>
                      </div>
                      <div className="card-body p-30">
                          <div className="row justify-content-center">
                              <div className="col-sm-6 col-md-8 col-lg-6 col-xl-5">
                                  <div className="card bank-info-card bg-bottom bg-contain bg-img" style={{ backgroundImage: 'url(https://6valley.6amtech.com/public/assets/back-end/img/bank-info-card-bg.png)' }}>
                                      <div className="border-bottom p-3">
                                          <h4 className="mb-0 fw-semibold">Holder Name : <strong>Fatema</strong></h4>
                                      </div>
                                      <div className="card-body position-relative">
                                          <img className="bank-card-img" width={78} src="https://6valley.6amtech.com/public/assets/back-end/img/bank-card.png" alt />
                                          <ul className="list-unstyled d-flex flex-column gap-4">
                                              <li>
                                                  <h3 className="mb-2">Bank Name :</h3>
                                                  <div>City Bank</div>
                                              </li>
                                              <li>
                                                  <h3 className="mb-2">Branch Name :</h3>
                                                  <div>Mirpur- 12</div>
                                              </li>
                                              <li>
                                                  <h3 className="mb-2">Account Number : </h3>
                                                  <div>12345678</div>
                                              </li>
                                          </ul>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
          </div>
  
        </div>
           

        </div>
    )
}

export default Mybankinfo
