import React from 'react'
import Header from '../Header'
import Sidebar from '../Sidebar'

const Editbankinfo = () => {
    return (
        <div className='container'>

            <Header />
            <div className='container row' style={{ paddingLeft: "0px", paddingRight: "0px", marginLeft: "0px" }}>
                <div className="col-lg-2 col-md-3" style={{ paddingLeft: "0px" }}>
                    <Sidebar />
                </div>

                <div className="col-lg-10 col-md-9" style={{ paddingLeft: "0px", marginTop: "60px" }}>
                    <div className="mt-3" style={{ textAlign: 'left' }}>
                        <div className="mb-3">
                            <h2 className="h1 mb-0 text-capitalize d-flex align-items-center gap-2">
                                <img width={20} src="https://6valley.6amtech.com/public/assets/back-end/img/my-bank-info.png" alt />
                                Edit Bank info
                            </h2>
                        </div>
                        <div className="row mt-4">
                            <div className="col-md-12">
                                <div className="card">
                                    <div className="card-header">
                                        <h4 className="mb-0 ">Edit Bank Info</h4>
                                    </div>
                                    <div className="card-body">
                                        <form>
                                            <div className="form-group">
                                                <div className="row">
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="name" className="title-color">Bank Name <span className="text-danger">*</span></label>
                                                        <input type="text" name="bank_name" defaultValue="City Bank" className="form-control" id="name" required />
                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="name" className="title-color">Branch Name <span className="text-danger">*</span></label>
                                                        <input type="text" name="branch" defaultValue="Mirpur- 12" className="form-control" id="name" required />
                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="account_no" className="title-color">Holder Name <span className="text-danger">*</span></label>
                                                        <input type="text" name="holder_name" defaultValue="Fatema" className="form-control" id="account_no" required />
                                                    </div>
                                                    <div className="col-md-6 mb-3">
                                                        <label htmlFor="account_no" className="title-color">Account No <span className="text-danger">*</span></label>
                                                        <input type="number" name="account_no" defaultValue={12345678} className="form-control" id="account_no" required />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="d-flex justify-content-end gap-2">
                                                <a className="btn btn-danger" href="https://6valley.6amtech.com/seller/profile/view">Cancel</a>
                                                <button type="submit" className="btn btn--primary" id="btn_update">Update</button>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>


        </div>
    )
}

export default Editbankinfo
